@import "fuelux-core.less";

@font-face {
	font-family: "fuelux";
	src:url("@{fuelux-font-path}fuelux.eot");
	src:url("@{fuelux-font-path}fuelux.eot?#iefix") format("embedded-opentype"),
		url(data:font-woff;base64,) format("woff"),
		url("@{fuelux-font-path}fuelux.ttf") format("truetype"),
		url("@{fuelux-font-path}fuelux.svg#fuelux") format("svg");
	font-weight: normal;
	font-style: normal;
}

//This could be an interesting addition...
//[data-icon]:before {
//	#fuelux > .icon-base-styles();
//	content: attr(data-icon);
//}

.fuelux {

	[class^="fueluxicon-"]:before,
	[class*=" fueluxicon-"]:before {
		#fuelux > .icon-base-styles();
	}

	.fueluxicon-loader-full:before {
		content: "\e000";
	}

	.fueluxicon-loader-1:before {
		content: "\e001";
	}

	.fueluxicon-loader-2:before {
		content: "\e002";
	}

	.fueluxicon-loader-3:before {
		content: "\e003";
	}

	.fueluxicon-loader-4:before {
		content: "\e004";
	}

	.fueluxicon-loader-5:before {
		content: "\e005";
	}

	.fueluxicon-loader-6:before {
		content: "\e006";
	}

	.fueluxicon-loader-7:before {
		content: "\e007";
	}

	.fueluxicon-loader-8:before {
		content: "\e008";
	}

	//tree

	.fueluxicon-bullet:before {
	  content: "\e009";
	}

}
